import Layout from '../components/layout';
import {withRouter} from 'next/router';
import axios from 'axios';


class Blog extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            posts: [],
        };
    }

    componentDidMount(){
        let url = 'https://blog.matrixki.com/wp-json/wp/v2/posts/'+this.props.router.query.id;
        axios.get(url)
        .then( res => {            
            return res.data;
        } )
        .then( post => {
            console.log(post);
            this.setState((state, props) => ({
                post: post,
            }));
        } );
    }

    render(){
        return (
            <Layout>
                <p>Blog page</p>
            </Layout>
        );
    }
};
export default Blog;



